.welcome {
  display: flex;
  /* height: 100vh; */
  margin: 5%;
}

.column {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 1rem;
  border-radius: 40px;
}

.column-content {
  width: 18.80rem;
  height: 33.7rem;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  opacity: 1;
  border: 5px solid black;
  border-radius: 30px;
}

.column.active .column-content p,
.column.active .column-content button {
  opacity: 1;
  margin: auto;
}

.column-content p {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  background-color: #f0f8ff80;
}

.column-content button {
  width: 6em;
  padding: 10px 20px;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 15px;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  background: linear-gradient(to right, #22ded9, #fc6a98);
  color: black;
  cursor: pointer;
  font-weight: 380;
  border: 3px solid black;
}

.column img {
  max-width: 80%;
  max-height: 80%;
  border-radius: 100px;
  border: 5px solid black;
}

.link {
  padding: 3.5rem;
}

/* Desktop Styles */
@media screen and (min-width: 1024px) {
  .welcome {
    justify-content: space-around;
    flex-wrap: wrap;
  }

  .column {
    flex-basis: 30%;
  }
}
/* Mobile Styles */
@media screen and (max-width: 767px) {
  .welcome {
    flex-direction: column;
  }

  .column {
    margin-bottom: 2rem;
  }
}


.thesis-button{
  text-decoration:none;
  color: black;
}