.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
  background-image: url('../images/hompage.png');
  background-size: cover;
  background-position: center;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 2rem;
  width: 100px;
}

.logo img {
  width: 200px; /* Adjust the width as needed */
}

.button {
  text-align: center;
  font-size: 2.7rem;
  font-weight: 380;
  background: linear-gradient(to right, hsl(176, 73%, 68%), hsl(336, 82%, 76%));
  color: black;
  width: 268px;
  border-radius: 30px;
  border: 3px solid black;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.button:hover {
  background: linear-gradient(to right, #22ded9, #fc6a98);
}

.Mindspace {
  width: 586px;
}

.TAULOGO {
  margin-top: 30px;
  width: 100px;
}

.language-options {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.language-option {
  margin: 0 2px;
  padding: 3px 4px;
  font-size: 1rem;
  background-color: #ffffff00;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.thesis-button {
  margin: 0 2px;
  padding: 3px 4px;
  font-size: 3rem;
  background-color: #ffffff00;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
 

/* Mobile Styles */
@media screen and (max-width: 767px) {
  .Mindspace {
    width: 100%;
    max-width: 400px; /* Limit the width to 400px for smaller screens */
  }

  .button {
    width: 100%; /* Make the button occupy the full width on mobile */
  }

  .language-options {
    margin-top: 10px;
    flex-wrap: wrap;
  }

  .language-option,
  .thesis {
    font-size: 1rem; /* Adjust the font size for smaller screens */
    margin: 2px;
    padding: 2px 4px;
  }
}

.mini-title{
  font-size: 1.5rem;
  font-weight: 400;
}